.TodoInmer {
    background-image: url('../../Images/RV/FondoRV.png');
    background-size: cover;    
    background-position: center center;
    color: white;
    font-family: 'Quicksand', sans-serif;
    min-height: 92.2vh;
}

.ContainRV {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowtitRV {
    width: 100vw;
}

.RowInfoRV{
    display: flex;
    align-items: center;
    flex-direction: column;
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .ContainRV {
        padding-top: 3.8rem;
    }

    .RowtitRV {
        padding-left: 5rem;        
    }

    .RowtitRV h1{
        font-size: 4rem;
        padding-top: 0.5rem;
    }

    .RowInfoRV img{
        width: 35rem;
    }

    .btnB {
        position: absolute;
        bottom: 2rem;
        left: 2rem;
    }

    .AudioRV {
        width: 30rem;
    }
}