.TodoEjemplos2 {
    background-image: url('../../Images/Fondo2.png');
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 92.2vh;
    font-family: 'Quicksand', sans-serif;
    color: white;
}

.ContaiEjemplosRV {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowEjemplosRV {
    display: flex;
    align-items: center;
}

.VideoEjemploRV {
    border-color: white;
    border-style: solid;
    border-width: 0.3rem;
}

.ColEjemploRV {
    font-weight: lighter;
}

.BtnBack {
    position: fixed;
    left: 2rem;
    bottom: 2rem;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .RowEjemplosRV {
        flex-direction: column;
    }

    .VideoEjemploRV {
        width: 18rem;
        height: 10.5rem;
    }

    .ColEjemploRV h2{
        font-size: 1.5rem;
    }

    .ColEjemploRV {
        margin: 1rem 0;
    }

    .RowTitEjemplosRV h1 {
        margin-top: 1rem;
    }

    .BtnBack {
        bottom: 1rem;
        left: 1rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 1200px) { 
    .RowTitEjemplosRV {
        padding: 0 1rem;
    }

    .RowEjemplosRV {
        flex-direction: column;
    }

    .ColEjemploRV h2{
        font-size: 2.3rem;
    }

    .ColEjemploRV p{
        font-size: 1.5rem;
    }

    .ColEjemploRV {
        padding: 0 2rem;
        margin: 2rem 0;
    }

    .RowTitEjemplosRV h1 {
        font-size: 3rem;
        margin-top: 2rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .TodoEjemplos2 {
        overflow: hidden;
    }

    .ContaiEjemplosRV {
        margin-bottom: 4.2rem;
    }

    .RowTitEjemplosRV {
        width: 100vw;
        text-align: left;
    }

    .RowTitEjemplosRV h1 {
        margin-left: 6rem;
        margin-bottom: 5rem;
        font-size: 4rem;
    }

    .RowEjemplosRV {
        padding: 0 5rem;
    }

    .ColEjemploRV h2{
        text-align: left;
    }

    .ColEjemploRV p{
        font-size: 1.5rem;
        text-align: left;
    }

    .VideoEjemploRV {
        width: 30rem;
        height: 16.9rem;
    }
}