.ContFunc {
    background-image: url('../Images/Fondo2.png');
    background-size: cover;
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
}

.contaiFuncio {
    height: 92.2vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) { 
    .ContFunc {
        background-image: url('../Images/FondoMovil.svg');
    }

    .contaiFuncio {
        align-items: center;
    }
    
    .conteneImg img {
        width: 18rem;
    }

    .conteneImg .BI {
        position: absolute;
        left: 4.7rem;
        top: 1rem; 
        width: 1.9rem;
        cursor: pointer;
    }

    .conteneImg .BI2 {
        position: absolute;
        right: 6rem;
        top: 2.8rem; 
        width: 1.9rem;
        cursor: pointer;
    }

    .conteneImg .BI3 {
        position: absolute;
        left: 2.2rem;
        bottom: 4.5rem;
        width: 1.9rem;
        cursor: pointer;
    }

    .conteneImg .BI4 {
        position: absolute;
        right: 1.7rem;
        bottom: 4.2rem;
        width: 1.9rem;
        cursor: pointer;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .contaiFuncio {
        align-items: center;
    }

    .conteneImg img {
        width: 40rem;
    }

    .conteneImg .BI {
        position: absolute;
        left: 10rem;
        top: 3rem; 
        width: 3.8rem;
        cursor: pointer;
    }

    .conteneImg .BI2 {
        position: absolute;
        right: 12rem;
        top: 6rem; 
        width: 3.8rem;
        cursor: pointer;
    }

    .conteneImg .BI3 {
        position: absolute;
        left: 4.5rem;
        bottom: 10rem;
        width: 3.8rem;
        cursor: pointer;
    }

    .conteneImg .BI4 {
        position: absolute;
        right: 3rem;
        bottom: 9rem;
        width: 3.8rem;
        cursor: pointer;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .conteneImg img {
        width: 60rem;
    }   

    .titlFunc h1 {
        margin-left: 5rem;
        font-size: 4rem;
    }

    .conteneImg .BI {
        position: absolute;
        left: 29rem;
        top: 5rem;
        width: 5rem;
        cursor: pointer;
        transition: transform .1s; /* Animation */
        margin: 0 auto;
    }

    .BI:hover{
        transform: scale(1.2);
    }

    .conteneImg .BI2 {
        position: absolute;
        right: 32rem;
        top: 10rem;
        width: 5rem;
        cursor: pointer;
        transition: transform .1s; /* Animation */
        margin: 0 auto;
    }

    .BI2:hover{
        transform: scale(1.2);
    }

    .conteneImg .BI3 {
        position: absolute;
        left: 20rem;
        bottom: 15rem;
        width: 5rem;
        cursor: pointer;
        transition: transform .1s; /* Animation */
        margin: 0 auto;
    }

    .BI3:hover{
        transform: scale(1.2);
    }

    .conteneImg .BI4 {
        position: absolute;
        right: 19rem;
        bottom: 14rem;
        width: 5rem;
        cursor: pointer;
        transition: transform .1s; /* Animation */
        margin: 0 auto;
    }

    .BI4:hover{
        transform: scale(1.2);
    }

    .Imgifogr {
        animation-name: example;
        animation-duration: 1s;
    }

    .Imgifogr {animation-timing-function: ease-in;}

    @keyframes example {
        0% {transform: translateX(15rem); opacity: 0;}
        50% {opacity: 1;}
    }

    .BI, .BI2, .BI3, .BI4 {
        animation-name: hola;
        animation-duration: 0.2s;
        animation-delay: 1s;  
        animation-fill-mode: both;      
    }

    @keyframes hola {
        0% {opacity: 0;}
        100% {opacity: 1;}
    }
}