.FondoFo {
    background-image: url('../Images/FondoFolleto.svg');
    background-repeat: no-repeat;
    background-position: bottom;
}

.contentFo {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Quicksand', sans-serif;
}

.TitleFolleto h1 {
    color: white;
    font-size: 3rem;
}

.DescriptionFolleto p {
    font-size: 1.2rem;
}

.b {
    display: flex;
    justify-content: center;
}

.b a {
    text-decoration: none;
    text-align: center;
    background-color:#2997D4;
    color: white;    
    border-radius: 2.5rem;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    font-weight: bolder;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .DescriptionFolleto {
        display: flex;
        flex-direction: column;
    }

    .TitleFolleto h1 {
        font-size: 1.5rem;
    }

    .b a {   
        padding-top: 0.25rem;
        width: 7.2rem;
        height: 2.5rem;
        font-size: 1.3rem;
    }

    .FondoFo {
        background-image: none;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) { 
    .b a {   
        padding-top: 0.35rem;
        width: 8rem;
        height: 3rem;
        font-size: 1.5rem;
    }

    .FondoFo {
        background-image: url('../Images/FondoFolletoIpad.svg');
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .Tx1 {
        margin-top: 3rem;
    }
    
    .DescriptionFolleto p {
        margin-bottom: 3rem;
    }

    .b {
        margin-bottom: 2rem;
    }

    .b a {   
        padding-top: 0.2rem;
        width: 8.5rem;
        height: 3rem;
        font-size: 1.7rem;
    }

    .Col2 .b {
        margin-bottom: 1rem;
    }

    .Col2 .Tx1 {
        margin-top: 2rem;
    }

    .Col2 h2 {
        margin-bottom: 3rem;
    }
}