.World {
    background-image: url('../Images/Fondo2.png');
    background-size: cover;    
    color: white;
    font-family: 'Quicksand', sans-serif;
    min-height: 92.2vh;
    display: flex;
    align-items: center;
}

.TitCont h1 {
    font-size: 3.5rem;
    font-weight: bold;
    
}

.Desc {
    text-align: left;
    font-size: 1.5rem;
    font-weight: lighter;
}

.Ver1 {
    width: 6rem;
    position: absolute;    
    cursor: pointer;
}

.Ver2 {
    width: 6rem;
    position: absolute;  
    cursor: pointer;  
}

.BodyPop {
    background-image: url("");
}

.Desc strong{
    font-weight: bolder;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .World {
        background-image: url('../Images/FondoMovil.svg');
        position: fixed;
    }

    .ContCont {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .Desc {
        text-align: center;
        font-size: 1rem;
    }

    .TitCont h1 {
        font-size: 1.7rem;
        margin-left: 0;
    }

    .Ver1 {
        width: 2.5rem;
        left: 7rem;
        top: 1rem;
    }

    .Ver2 {
        width: 2.5rem;
        right: 6.5rem;
        bottom: 1.8rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) { 
    .Ver1 {
        width: 4.5rem;
        left: 14rem;
        top: 3.2rem;
    }

    .Ver2 {
        width: 4.5rem;
        right: 13.2rem;
        bottom: 5.2rem;
    }   
    
    .Desc p{
        text-align: center;
    }

    .TitCont {
        justify-content: center;
    }

    .TitCont h1{
        font-size: 3rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .Desc {
        margin-left: 4rem;
        /* margin-top: 4rem; */
    }

    .TitCont h1 {
        margin-bottom: 8.3rem;
        margin-left: 5rem;
    }
    
    .Infografia {
        margin-left: 3rem;
    }

    .Ver1 {
        left: 22.5rem;
        top: 5.5rem;
    }

    .Ver2 {
        right: 18rem;
        bottom: 8.2rem;
    }

    .Desc strong{
        font-weight: bold;
    }

    .Ver1 {
        transition: transform .2s; /* Animation */
        margin: 0 auto;
    }

    .Ver1:hover {
        transform: translateY(1rem);
    }

    .Ver2 {
        transition: transform .2s; /* Animation */
        margin: 0 auto;
    }

    .Ver2:hover {
        transform: translateY(-1rem);
    }
}