.Alles {
    background-image: url('../Images/Fondo2.png');
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 92.2vh;
    font-family: 'Quicksand', sans-serif;
    color: white;
}

.cont p {
    font-weight: lighter;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .Alles {
        background-image: url('../Images/FondoMovil.svg');      
        position: fixed;
    }

    .Contenedor {
        padding-bottom: 2rem;
    }

    .nada {
        max-width: 19rem;
        max-height: 11rem;    
        border: white;
        border-style: solid;
        border-width: 0.3rem;
    }

    .content {
        display: flex;
        flex-direction: column-reverse;
    }

    .TitleTec {
        font-size: 1.5rem;
    }

    .Boton {
        margin-top: 0.8rem;
    }   

    .cont p {
        margin-bottom: 2rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 1200px) { 
    .content {
        display: flex;
        flex-direction: column-reverse;
    }

    .cont p {
        font-size: 1.7rem;
    }

    .nada {
        width: 40rem;
        max-height: 30rem;  
        border: white;
        border-style: solid;
        border-width: 0.3rem;  
    }

    .Boton {
        margin-top: 1.5rem;
    }

    .cont strong {
        font-weight: bolder;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {  
    .Alles {
        display: flex;
        align-items: center;
        height: 90vh;
    }

    .Contenedor {
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .Boton {
        position: absolute;
        z-index: 1;
        right: 3rem;
        bottom: 5rem;
    }

    .nada {
        min-width: 117rem;
        min-height: 53rem;
        position: relative;
        z-index: 0;
        border: white;
        border-style: solid;
        border-width: 0.3rem;
    }

    .TitleTec {
        position: absolute;
        z-index: 1;
        left: 3rem;
        top: 2rem;
        font-size: 2.5rem;
        font-weight: bold;
    }

    .cont p {
        display: none;
    }
}