.TodoEspacial {
    background-image: url('../../Images/Fondo2.png');
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 92.2vh;
    color:white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
}

#ConceptoEs, #AplicacionesEs, #MarcadoresRA {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 92.2vh;
    margin: 0;
}

.ContaiEspacial {
    display: flex;
    flex-direction: column;
    align-items: center;   
}

.TitRAEspacial {
    width: 100%;
}

.RowInfoEspacial {
    display: flex;
    align-items: center;
}

.VideoEspacial {
    border-color: white;
    border-style: solid;
}

.BotVolType {
    position: fixed;
}

.BottombarE {
    width: 100%;
    background-color: #063E66 ;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
}

.ContaiRAEspa {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.RowTitRAAppsEs {
    width: 100%;
    display: flex;
}

.MenuBo {
    display: flex;
}

.MenuBo .Campo {
    color: white;
    font-family: 'Quicksand', sans-serif;
}

.RowAppEs {
    display: flex;
    align-items: center;
}

.aLink2 span{    
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.aLink2 span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.aLink2:hover span {
    padding-right: 25px;
}

.aLink2:hover span:after {
    opacity: 1;
    right: 0;
}

