/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .RowInfoEspacial {
        flex-direction: column;
    }

    .RowInfoEspacial p {
        font-weight: lighter;
        margin-bottom: 2rem;
    }

    .VideoEspacial {
        width: 19rem;
        height: 11rem;
        border-width: 0.3rem;
    }

    .BotVolType {
        bottom: 2.7rem;
        left: 1rem;
    }

    .RowTitRAAppsEs {
        justify-content: center;
    }

    .MenuBo .Campo {
        font-size: 0.7rem;
    }

    .RowAppEs {
        flex-direction: column-reverse;
    }

