.card1,
.card2,
.card3,
.card4,
.card5,
.card6,
.card7,
.card8 {
  position: absolute;
  background-position: center center;
  background-repeat: no-repeat;
  will-change: transform;
}

.card1 {
    width: 55rem;
    height: 65rem;
    background-image: url(../Images/Portada/Fondo.svg);
}

.card2 {
    width: 8rem;
    height: 8rem;
    background-image: url(../Images/Portada/Cubo1.svg);
}

.card3 {
    width: 25rem;
    height: 25rem;
    background-image: url(../Images/Portada/personaje.svg);
}

.card4 {
    width: 8rem;
    height: 8rem;
    background-image: url(../Images/Portada/Cubo2.svg);
}

.card5 {
    width: 10rem;
    height: 10rem;
    background-image: url(../Images/Portada/Planeta1.svg);
}

.card6 {
    width: 6rem;
    height: 6rem;
    background-image: url(../Images/Portada/Screen.svg);
}

.card7 {
    width: 4.8rem;
    height: 4.8rem;
    background-image: url(../Images/Portada/media.svg);
}

.card8 {
    width: 6rem;
    height: 6rem;
    background-image: url(../Images/Portada/Cubo1.svg);
}

.container2 {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.All {
    background-image: url('../Images/Fondo2.png');
    background-size: cover; 
    min-height: 92.2vh; 
}

/* .Content {
    align-items: center;
} */

.Section1 {
    color: white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
    text-align: start;
}

h1 {
    font-size: 5rem;
    margin-bottom: 3rem;
    font-weight: bold;    
}

.Section1 p {
    font-size: 2rem;
    font-weight: lighter;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {     
    .All {
        position: fixed;
        height: 100vh; 
        background-image: url('../Images/FondoMovil.svg');
    }

    .Container {
        height: 90%;
        display: flex;
    }

    .Content {
        margin-top: 2rem;
    }

    .text {
        padding: 0rem 0.8rem;
    }

    h1 {
        font-size: 1.7rem;
        margin-bottom: 1.5rem;
    }

    .Section1 p {
        font-size: 1rem;
        margin-bottom: 1.5rem;
    }

    .Section1 {
        text-align: center;
        justify-content: center;
    }

    .Section2 {
        margin-top: 0.1rem;
    }

    .Section2 img{
        width: 18rem;
    }

    .container2 {
        display: none;
    }
}

/*Small devices (landscape phones, 576px and up)*/
@media (min-width: 576px) and (max-width: 767.98px) {  
    
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) { 
    .container2 {
        display: none;
    }

    .text {
        padding: 0 2rem;
    }

    .Section1 {
        display: flex;
        align-items: center;
        text-align: center;
        margin-top: 3rem;
    }

    .Section1 h1{
        font-size: 3rem;
    }

    .Section1 p {
        margin-bottom: 3rem;
    }

    .ImPor {
        width: 40rem;
        margin-top: 4rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {  
    .All {
        position: fixed;    
        background-position: center;
        min-width: 100vw;
    }

    .ColImgPortada {
        height: 100vh;
    }

    .ImPor {
        display: none;
    }

    .text{ 
        padding-left: 5rem;
        margin-top: 10rem;
    }

    .Section1 p{
        margin-top: 5rem;
        margin-bottom: 4rem;
    }
}