.AbAll {
    background-image: url('../Images/Fondo2.png');
    background-size: cover;
    display: flex;
    align-items: center;
    min-height: 92.2vh;
}

.AboContent{
    color: white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Logo img{
    width: 12rem;
}

.TextSec{
    display: flex;
    justify-content: center;
}

.TextSec p {
    font-size: 2rem;
    text-align: left;
    font-weight: lighter;
}

.TextSec h1 {
    text-align: left;
    font-size: 3.5rem;
    font-weight: bold;
}

.Perfilimg img {
    width: 8rem;
}

.InfoDesa {
    display: flex;
    align-items: center;
}

.Desarrollo h2 {
    font-size: 2.5rem;
    font-weight: normal;    
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .AbAll {
        background-image: url('../Images/FondoMovil.svg');
        /* position: fixed; */
    }

    .AboContent{
        max-height: 100vh;
    }

    .Logo img{
        margin-top: 1.3rem;
        width: 7rem;
    }

    .TextSec p {
        font-size: 1rem;
        text-align: center;
    }

    .TextSec h1 {
        text-align: center;
        font-size: 1.7rem;
        margin-top: 1.2rem;
    }

    .Desarrollo h2 {
        font-size: 1.4rem;
    }

    .InfoDesa {
        flex-direction: column;
    }

    .Perfilimg img {
        width: 5rem;
        margin-bottom: 0.5rem;
    }

    .InfoName h3 {
        font-size: 1rem;
        margin-bottom: 0.4rem;
        font-weight: lighter;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .AboContent{
        height: 92.2vh;        
        display: flex;
        justify-content: center;
    }

    .TextSec p {
        margin: 0rem 6rem;
    }

    .TextSec h1 {
        margin-left: 4rem;
        margin-bottom: 4.5rem;
        margin-top: 1rem;
    }

    .Desarrollo {
        text-align: left;
        margin-top: 4rem;
        margin-left: 6rem;
    }

    .Desarrollo h2 {
        margin-top: 5rem;
        margin-bottom: 2.3rem;
    }

    .InfoName{
        margin-left: 3rem;
    }

    .InfoName h3 {
        margin-bottom: 1.2rem;
        font-size: 1.8rem;
        font-weight: lighter;
    }

    .Logo {
        width: 95vw;
        display: flex;
        justify-content: flex-end;
        margin-top: 7rem;
    }
}