html{scroll-behavior: smooth;}

.TodoExp {
    background-image: url('../Images/Fondo2.png');
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
}

#DisEx, #Fase1, #Estructura, #Fase2, #Fase3 {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin: 0;
} 

.RowExpe {
    display: flex;
    align-items: center;
}

.ContainFase1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ContainFase2 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ContainFase3 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowConteniFase1 {
    display: flex;
    align-items: center;
}

.RowConteniFase2 {
    display: flex;
    align-items: center;
}

.ColInfoFase1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ColInfoFase2 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowInfoFase1 {
    display: flex;
}

.RowInfoFase2 {
    display: flex;
}

.ColEntregablesFase1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ColEstructExperi {
    display: flex;
    flex-direction: column;
}

.RowInfoEntreFase1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowInfoEstruExp p{
    font-weight: lighter;
}

.ColInfoFase3 {
    display: flex;
}

.ColInfoFase3 p {
    font-weight: lighter;
}

.RowContentFase3 {
    display: flex;
}

@import "~react-image-gallery/styles/css/image-gallery.css";

.carousel-inner {
    border: white;
    border-style: solid;
    border-width: 0.5rem;
}

