    }

    .RowCambioFase1 {
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
    }

    .RowInfoEstruExp {
        display: flex;
        flex-direction: column;
    }

    .TextEntreFase1 p{
        font-weight: lighter;
        padding: 0 1rem;
    }

    .ColInfoFase3 {
        flex-direction: column;
    }

    .RowContentFase3 {
        flex-direction: column;
    }

    .carousel-inner {
        border-width: 0.3rem;
    }
}

