    }

    .IndInici h2 {
        position: relative;
        bottom: 2rem;
        left: 55rem;
    }

    .IndInici h2:after{
        content: '\21E3';
        font-size: 4rem;
        animation: mymove2 0.8s infinite;
        position: relative;
    }

    .Fas1 h2 {
        position: relative;
        bottom: -3rem;
        left: 50.1rem;
    }

    .Fas1 h2:after{
        content: '\21E3';
        font-size: 4rem;
        animation: mymove2 0.8s infinite;
        position: relative;
    }

    .Fas2 h2 {
        position: relative;
        bottom: -5rem;
        left: 55rem;
    }

    .Fas2 h2:after{
        content: '\21E3';
        font-size: 4rem;
        animation: mymove2 0.8s infinite;
        position: relative;
    }

    .Fas2 a {
        text-decoration: none;
        color: white
    }

    .Fas3 h2 {
        position: relative;
        bottom: -1.7rem;
        left: 55rem;
    }

    .Fas3 h2:after{
        content: '\21E3';
        font-size: 4rem;
        animation: mymove2 0.8s infinite;
        position: relative;
    }

    .Fas3 a {
        text-decoration: none;
        color: white
    }

    @keyframes mymove2 {
        0% {top: -10px;}
        100% {top: 10px;}
    }

    .IndInici a {
        text-decoration: none;
        color: white
    }

    .Fas1 a {
        text-decoration: none;
        color: white
    }

    .RowTitFase1 h1{
        font-size: 4rem;
    }

    .RowTitFase2 h1 {
        font-size: 4rem;
    }

    .PaddingInfoFas1 {
        padding-right: 10rem;
    }

    .PaddingInfoFas1 p{
        margin-bottom: 4rem;
    }

    .imgzoE {
        transition: transform .1s; /* Animation */
        margin: 0 auto;
    }

    .imgzoE:hover {
        transform: scale(1.1); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
    }

    .hola .tooltiptext {
        visibility: hidden;
        width: 120px;
        background-color: black;
        color: #fff;
        text-align: center;
        border-radius: 6px;
        padding: 5px 0;
        top: 4rem;
        right: 11rem;
      
        /* Position the tooltip */
        position: absolute;
        z-index: 1;
    }

    .hola:hover .tooltiptext {
        visibility: visible;
    }

    .hola .tooltiptext::after {
        content: " ";
        position: absolute;
        top: 50%;
        left: 100%; /* To the right of the tooltip */
        margin-top: -5px;
        border-width: 5px;
        border-style: solid;
        border-color: transparent transparent transparent black;
    }

