.TodoSeInmer {
    background-image: url('../../Images/RV/FondoRV.png');
    background-size: cover; 
    background-position: center center;   
    color: white;
    font-family: 'Quicksand', sans-serif;
    min-height: 92.2vh;
}

.ContainRVSe {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowtitRVSe {
    width: 100vw;
}

.RowInfoRVSe{
    display: flex;
    align-items: center;
    flex-direction: column;
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .ContainRVSe {
        padding-top: 3.8rem;
    }

    .RowtitRVSe {
        padding-left: 5rem;        
    }

    .RowtitRVSe h1{
        font-size: 4rem;
        padding-top: 0.5rem;
    }

    .btnB {
        position: absolute;
        bottom: 2rem;
        left: 2rem;
    }

    .RowInfoRVSe img{
        width: 30rem;
    }

    .AudioRVSe {
        width: 30rem;
        margin-top: 3rem;
    }
}