.TodoTools {
    background-image: url('../../Images/Fondo2.png');
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
}

.ContaiTools1 {
    display: flex;
    flex-direction: column;
}

.RowInfoTools {
    display: flex;
}

.RowInfoTools p{
    font-weight: lighter;
}

.RowInfoTools a{
    text-decoration: none;
    text-align: center;
    background-color:white;
    color: #2997D4;    
    border-radius: 2.5rem;
    border-style: solid;
    border-width: 0.2rem;
    border-color: #2997D4;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    font-weight: bold;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .ContaiTools1 {
        padding-top: 1rem;
    }

    .RowInfoTools{
        flex-direction: column;
    }    
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {


    .ContaiTools1 {
        padding-top: 2.1rem;
    }

    .RowTitTools{
        padding-left: 5rem;
    }

    .RowTitTools h1 {
        font-size: 4rem;
    }

    .RowInfoTools {
        margin-top: 4rem;
        padding-left: 25rem;
        padding-right: 10rem;
        align-items: center;
    }

    .RowInfoTools p {
        font-size: 1.7rem;
        text-align: left;
        margin-bottom: 3rem;
    }

    .RowInfoTools a {
        font-size: 1.7rem;
        padding: 0.2rem 1rem;
    }

    .ColImgTools img {
        width: 17rem;
    }
    
    .ColImgTools {
        display: flex;
        align-items: flex-start;
        margin-left: 7rem;
    }




