.TodoEjemplos {
    background-image: url('../../Images/Fondo2.png');
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 92.2vh;
    font-family: 'Quicksand', sans-serif;
    color: white;
}

.ContaiEjemplosRA {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowEjemplosRA {
    display: flex;
    /* align-items: center; */
}

.VideoEjemploRA {
    border-color: white;
    border-style: solid;
    border-width: 0.3rem;
}

.ColEjemploRA {
    font-weight: lighter;
}

.BtnBack {
    position: fixed;
    left: 2rem;
    bottom: 2rem;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .RowEjemplosRA {
        flex-direction: column;
    }

    .VideoEjemploRA {
        width: 18rem;
        height: 10.5rem;
    }

    .ColEjemploRA h2{
        font-size: 1.5rem;
    }

    .ColEjemploRA {
        margin: 1rem 0;
    }

    .RowTitEjemplosRA h1 {
        margin-top: 1rem;
    }

    .BtnBack {
        bottom: 1rem;
        left: 1rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 1200px) { 
    .RowTitEjemplosRA {
        padding: 0 1rem;
    }

    .RowEjemplosRA {
        flex-direction: column;
    }

    .ColEjemploRA h2{
        font-size: 2.3rem;
    }

    .ColEjemploRA p{
        font-size: 1.5rem;
    }

    .ColEjemploRA {
        padding: 0 2rem;
        margin: 2rem 0;
    }

    .RowTitEjemplosRA h1 {
        font-size: 3rem;
        margin-top: 2rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    /* .TodoEjemplos {
        overflow: hidden;
    } */

    .ContaiEjemplosRA {
        margin-bottom: 4.2rem;
    }

    .RowTitEjemplosRA {
        text-align: left;
    }

    .RowTitEjemplosRA {
        width: 100vw;
    }

    .RowTitEjemplosRA h1 {
        margin-left: 6rem;
        margin-bottom: 5rem;
        font-size: 4rem;
    }

    .RowEjemplosRA {
        padding: 0 5rem;
        margin-bottom: 2rem;
    }

    .ColEjemploRA {
        padding: 0 2rem;
    }

    .ColEjemploRA h2{
        text-align: left;
    }

    .ColEjemploRA p{
        font-size: 1.5rem;
        text-align: justify;
    }

    .VideoEjemploRA {
        width: 30rem;
        height: 16.9rem;
    }
}