html{scroll-behavior: smooth;}

.TodoSteamSection {
    background-image: url('../../Images/Fondo2.png');
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
}

#IntroSectionSteam, #AboutSTEAM, #Conectar, #EjesConectar, #ExperienciasConectar {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 92.2vh;
    margin: 0;
}

.ContaiIntroSteam {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowTextIntroSteam p{
    font-weight: lighter;
}

.RowImgDosPartes {
    display: flex;
}

.ContaiAboSteam {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.DivInfoAS {
    display: flex;
}

.ColInfoAbSteam {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowInfoAbSteam p{
    font-weight: lighter;
}

.ContaiConectar {
    display: flex;
    flex-direction: column;
}

.RowConectar {
    display: flex;
}

.ColInfoConectar p{
    font-weight: lighter;
}

.RowContenEjes {
    display: flex;
}

.RowContenEjes {
    display: flex;
}

.ColTextEjes p{
    font-weight: lighter;
}

.ColTextExpCon p{
    font-weight: lighter;
}

.InstrucciónInicio {
    display: flex;
}

.InstrucciónInicio p{
    font-style: italic;
}

.RowTextIntroSteam strong {
    font-weight: bolder;
}

.ColInfoConectar a {
    font-style: italic;
    color: white;
}

.ColImgConectarr p{
    font-style: italic;
}

.ColTextExpCon a{
    text-decoration: none;
    text-align: center;
    background-color:white;
    color: #2997D4;    
    border-radius: 2.5rem;
    border-style: solid;
    border-width: 0.2rem;
    border-color: #2997D4;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    font-family: 'Quicksand', sans-serif;
    font-weight: bold;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .RowTitIntroSteam h1{
        margin-top: 1rem;
    }

    .RowTextIntroSteam p{
        padding: 0 1rem;
    }

    .RowImgDosPartes {
        flex-direction: column;
    }

    .RowImgDosPartes img{
        width: 15rem;
    }

    .ContaiAboSteam {
        flex-direction: column;
    }

    .DivInfoAS {
        flex-direction: column;
    }

    .RowTitAbSteam {
        justify-content: center;
        padding-top: 5.5rem;
    }
    
    .AudioSteam {
        width: 13rem;
    }

    .RowInfoAbSteam{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .ColImgSteamEstr img {
        width: 18rem;
        margin-top: 2rem;
    }

    .ContaiConectar {
        align-items: center;
        margin-top: 5.5rem;
    }

    .RowConectar {
        flex-direction: column;
    }

    .ColInfoConectar img {
        width: 18rem;
        margin-bottom: 2rem;
    }

    .ColImgConectarr img{
        width: 12rem;
        margin: 1rem 0;
    }

    .Cardsejes {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .RowContenEjes {
        flex-direction: column;
    }

    .RowTitEjes {
        margin-top: 6rem;
    }

    .ColImgEjes img {
        width: 18rem;
    }

    .RowTitExpConec h1{
        padding: 0 1rem;
    }

    .ColImgExpCon img {
        width: 18rem;
    }

    .ContaiExperConec {
        margin-top: 2rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .RowTitIntroSteam h1{
        font-size: 2.7rem;
        margin-top: 2rem;
    }

    .RowTextIntroSteam p{
        font-size: 1.5rem;
        padding: 0 2rem;
    }

    .RowImgDosPartes img{
        width: 19rem;
        margin-top: 2rem;
    }

    .ContaiAboSteam {
        flex-direction: column;
    }

    .RowTitAbSteam {
        justify-content: center;
        padding-top: 5.5rem;
    }

    .DivInfoAS {
        flex-direction: column;
    }

    .RowTitAbSteam h1 {
        font-size: 2.7rem;
    }

    .RowInfoAbSteam p{
        font-size: 1.5rem;
        padding: 0 1rem;
    }

    .RowInfoAbSteam{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .AudioSteam {
        width: 20rem;
        margin-top: 1rem;
    }

    .ColImgSteamEstr img {
        width: 30rem;
        margin-top: 3rem;
    }

    .ContaiConectar {
        align-items: center;
    }

    .RowConectar {
        flex-direction: column;
    }

    .ColInfoConectar img {
        width: 35rem;
        margin-bottom: 2rem;
    }

    .ContaiConectar h1 {
        font-size: 2.7rem;
        margin-top: 5.5rem;
    }

    .ColInfoConectar p{
        font-size: 1.5rem;
        padding: 0 1rem;
        margin-bottom: 2rem;
    }

    .ColImgConectarr img{
        width: 15rem;
        margin: 2rem 2rem;
    }

    .Cardsejes {
        display: flex;
        justify-content: center;    
    }

    .ColImgConectarr p{
        font-size: 1.3rem;
        font-weight: lighter;
    }

    .RowContenEjes {
        flex-direction: column;
    }

    .RowTitEjes h1{
        font-size: 2.7rem;
        padding: 0 1.5rem;
    }

    .ContaiEjes {
        margin-top: 5rem;
    }

    .ColTextEjes p{
        font-size: 1.5rem;
        padding: 0 2rem;
    }

    .ColImgEjes img{
        width: 30rem;
    }

    .RowTitExpConec h1{
        font-size: 2.7rem;
        padding: 0 1.5rem;
    }

    .ColImgExpCon img{
        width: 30rem;
    }

    .ColTextExpCon p{
        font-size: 1.5rem;
        padding: 0 2rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .RowTitIntroSteam{
        width: 100%;   
        padding-left: 4rem;
    }

    .RowTitIntroSteam h1 {
        font-size: 4rem;
    }

    .RowTextIntroSteam p{
        font-size: 1.7rem;
        padding: 0 5rem;
        text-align: left;
    }

    .RowImgDosPartes img{
        width: 25rem;
        margin: 0.5rem 4rem;
    }

    .imgST {
        transition: transform .1s; /* Animation */
        margin: 0 auto;
    }

    .imgST:hover {
        transform: scale(1.05); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
    }

    .RowTitAbSteam {
        width: 100%;
        padding-left: 4rem;
    }

    .RowTitAbSteam h1 {
        font-size: 4rem;
    }

    .RowInfoAbSteam {
        padding-left: 5rem;
        margin-top: 5rem;
    }

    .RowInfoAbSteam p{
        font-size: 1.7rem;
        text-align: left;
    }

    .AudioSteam {
        width: 30rem;
        margin-top: 2rem;
    }

    .ColImgSteamEstr img {
        width: 40rem;
    }

    .ContaiAboSteam {
        margin-top: 7.5rem;
    }

    .ContaiConectar {
        padding-left: 6rem;
        margin-top: 7.2rem;
    }

    .ColInfoConectar p{
        font-size: 1.7rem;
        text-align: left;
        margin-top: 2rem;
    }

    .ColInfoConectar img{
        width: 40rem;
    }

    .ColImgConectarr img {
        width: 20rem;
        padding: 0 1rem;
    }

    .ColImgConectarr{
        padding-top: 4rem;
    }

    .ColImgConectarr p{
        font-size: 1.3rem;
        font-weight: lighter;
        margin-top: 2rem;
    }

    .ContaiEjes {
        padding-left: 6rem;
        margin-top: 7rem;
    }

    .ColImgEjes img {
        width: 40rem;
    }

    .ColTextEjes p{
        font-size: 1.7rem;
        text-align: left;
    }

    .RowContenEjes {
        display: flex;
        align-items: center;
    }

    .ContaiExperConec {
        display: flex;
        flex-direction: column;
        margin-top: 5rem;
    }

    .ColImgExpCon img {
        width: 40rem;
    }

    .RowTitExpConec {
        padding-left: 5rem;
    }

    .ColTextExpCon p{
        font-size: 1.7rem;
        text-align: left;
        padding-right: 7rem;
    }

    .RowContenExpConec {
        display: flex;
        align-items: center;
    }   
    
    .InstrucciónInicio p{
        font-size: 1.4rem;
        margin-top: 2rem;
    }

    .F1 h2 {
        position: relative;
        top: 12rem;
        left: 27rem;
    }

    .F1 h2:after{
        content: '\21E3';
        font-size: 4rem;
        animation: mymove 0.8s infinite;
        position: relative;
    }

    @keyframes mymove {
        0% {top: -10px;}
        100% {top: 10px;}
    }

    .F2 h2{
        position: relative;
        left: 50.8rem;
    }

    .F2 h2:after{
        content: '\21E3';
        font-size: 4rem;
        animation: mymove 0.8s infinite;
        position: relative;
    }

    .F3 h2{
        position: relative;
        left: 28rem;
        top: 13rem;
    }

    .F3 h2:after{
        content: '\21E3';
        font-size: 4rem;
        animation: mymove 0.8s infinite;
        position: relative;
    }

    .RowTextIntroSteam {
        justify-content: center;
    }

    .RowTitConectar h1 {
        font-size: 4rem;
    }

    .RowTitEjes h1 {
        font-size: 4rem;
    }

    .RowTitExpConec h1{
        font-size: 4rem;
    }

    .ColTextExpCon a{
        font-size: 1.7rem;
        padding: 0.1rem 1rem;
    }
}