.TodoTech {
    background-image: url('../../Images/Fondo2.png');
    background-size: cover;    
    color: white;
    font-family: 'Quicksand', sans-serif;
    min-height: 92.2vh;
}

.ContaiTech {
    display: flex;
    flex-direction: column;
}

.RowTextTech{
    display: flex;
}

.RowTextTech p{
    font-weight: lighter;
}

.ColTextTech2 a{
    text-decoration: none;
    text-align: center;
    background-color:white;
    color: #2997D4;    
    border-radius: 2.5rem;
    border-style: solid;
    border-width: 0.2rem;
    border-color: #2997D4;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    font-weight: bold;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .ContaiTech {
        padding-top: 1rem;
    }

    .RowTextTech{
        flex-direction: column;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .ContaiTech {
        padding-top: 1rem;
    }

    .RowTitTech {
        justify-content: center;
        padding: 0 2rem;
    }

    .RowTextTech p {
        font-size: 1.5rem;
        padding: 0 2rem;
    }

    .ContaiTech h1{
        font-size: 3rem;
    }

    .RowTextTech{
        flex-direction: column;
    }

    .ColImgPer img{
        width: 30rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .ContaiTech {
        padding-top: 3.8rem;
    }

    .RowTitTech{
        padding-left: 5rem;
    }

    .RowTitTech h1 {
        font-size: 4rem;
    }

    .RowTextTech {
        margin-top: 4rem;
        padding-left: 20rem;
        padding-right: 10rem;
        align-items: center;
    }

    .RowTextTech p{
        text-align: left;
        font-size: 1.7rem;
    }

    /* .ColInfoTech{
        padding-left: 5rem;
    } */

    .ColTextTech2 a {
        font-size: 1.6rem;
        padding: 0.3rem 1rem;
    }

    .ColImgTech img {
        width: 35rem;
    }

    .imgTechn {
        width: 49rem;
        height: 28rem;        
        border-width: 0.5rem;
        animation-name: tecin;
        animation-duration: 0.5s;
    }

    .imgTechn {animation-timing-function: ease-in;}

    @keyframes tecin {
        0% {transform: translateX(15rem); opacity: 0;}
        50% {opacity: 1;}
    }
}