html{scroll-behavior: smooth;}

.AcercamientoRA {
    background-image: url('../../Images/Fondo2.png');
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
}

#AboutAcercamiento, #ElementosAcerca, #BrochureAcercamiento, #FichaAcercamiento, #PlaneadorAcercamiento{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 92.2vh;
    margin: 0;
}

.RowImgCont {
    width: 100%;
    display: flex;
    justify-content: center;
}

.RowImgCont img {
    width: 4rem;
}

.ContaiAcerca {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowTitAcerca {
    width: 100%;
}

.RowInfoAcerca {
    width: 100%;
    display: flex;
    align-items: center;
}

.ColInfoAcercam {
    display: flex;
}

.RowElemeAcer {
    display: flex;
    align-items: center;
}

.ColSeleEleAcer {
    display: flex;
    align-items: center;
}

.ElemAcer {
    display: flex;
    justify-content: center;
}

.ColInfoElemenAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowTitElemAcer {
    width: 100%;
}

.ColInfoBroAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ColImaFichaAcerca {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ColInfoFichaAcerca{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.RowTextoFichaAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowInfoExtraFichaAcer {
    display: flex;
    align-items: center;
}

.ContainPlaneadorAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowPlaneAcer {
    width: 100%;
}

.RowInfoPlaneAcer {
    display: flex;
    align-items: center;
}

.ColInfoPlaneAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.ColFasesPlaneAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowTitFasesPlaneAcer {
    width: 100%;
    display: flex;
}

.RowTitActvPlaneAcer {
    width: 100%;
    display: flex;
}

.RowActividadesPlaneAcer {
    display: flex;
}

.ColActiPlanAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowTitActivi1PlanAcer {
    width: 100%;
    display: flex;
}

.RowConteAct1PlanAcer {
    display: flex;
    align-items: center;
}

.RowContenListPlanAcer {
    width: 100%;
    display: flex;
    align-items: center;
}

.ColListPlanAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowListRePlanAcer {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
}

.ColImgPlaneAcer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowImagPlanAc {
    width: 100%;
    display: flex;
    justify-content: center;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .RowElemeAcer {
        flex-direction: column;
    }

    .RowTitAcerca h1{
        margin-top: 1rem;        
    }

    .RowInfoAcerca {
        flex-direction: column-reverse;
    }

    .ColInfoAcercam {
        flex-direction: column;
    }

    .ColInfoAcercam h2{
        font-size: 1.4rem;
    }

    .ColInfoAcercam p{
        font-weight: lighter;
    }

    .ColProposito {
        margin-bottom: 1rem;
    }

    .ColObjetivo {
        margin-bottom: 2rem;
    }

    .BRegr {
        position: fixed;
        bottom: 1rem;
        left: 2rem;
    }

    .ColSeleEleAcer {
        flex-direction: column;
    }

    .RowInfoExtraFichaAcer {
        flex-direction: column;
    }

    .RowInfoPlaneAcer {
        flex-direction: column;
    }

    .RowImgfasesPlaneAcer {
        width: 100%;
        display: flex;
        justify-content: center;
    }  
    
    .ColFasesPlaneAcer {
        margin-top: 2rem;
    }

    .ColFasesPlaneAcer img {
        width: 13rem;
        margin-top: 0.7rem;
    }

    .ColInfoPlaneAcer p {
        font-weight: lighter;
    }

    .RowTitFasesPlaneAcer {
        justify-content: center;
    }

    .RowTitFasesPlaneAcer h2{
        font-size: 1.7rem;
    }

    .RowTitActvPlaneAcer {
        justify-content: center;
        margin-top: 1.2rem;
    }

    .RowTitActvPlaneAcer h1{
        font-size: 1.7rem;
        font-weight: normal;
    }

    .RowActividadesPlaneAcer {
        flex-direction: column;
    }

    .RowConteAct1PlanAcer {
        flex-direction: column;
    }

    .RowTitActivi1PlanAcer {
        justify-content: center;
    }

    .RowTitActivi1PlanAcer h3{
        font-size: 1.4rem;
    }

    .RowConteAct1PlanAcer p {
        font-weight: lighter;
    }

    .RowConteAct1PlanAcer img {
        width: 15rem;
    }

    .RowContenListPlanAcer {
        flex-direction: column;
    }

    .RowListRePlanAcer {
        margin-top: 1.2rem;
    }

    .RowListRePlanAcer li {
        font-weight: lighter;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .RowInfoAcerca {
        flex-direction: column-reverse;
        margin-bottom: 2rem;
    }

    .RowTitAcerca h1{
        font-size: 2.5rem;
        margin-top: 1rem;
    }

    .ColInfoAcercam {
        text-align: left;
    }

    .ColInfoAcercam p{
        font-size: 1.5rem;
        font-weight: lighter;
    }

    .ImaAce img {
        width: 37rem;
    }

    .BRegr {
        position: fixed;
        bottom: 2rem;
        left: 2rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .RowTitAcerca h1{
        margin-left: 5rem;        
    }

    .RowInfoAcerca {
        padding-left: 5rem;
    }
    
    .ColInfoAcercam {
        text-align: left;
    }

    .ColInfoAcercam p{
        font-size: 1.7rem;
        font-weight: lighter;
    }

    .BRegr {
        position: fixed;
        bottom: 2rem;
        left: 3rem;
    }

    .ColInfoElemenAcer {
        text-align: left;
    }

    .RowTitElemAcer h1 {
        margin-left: 5rem;
        margin-bottom: 5rem;
    }

    .RowTextElemAcer {
        padding: 0 6rem;
    }
    
    .AudioEle {
        width: 25rem;
        margin-top: 3rem;
    }

    .RowTextElemAcer p {
        font-size: 1.7rem;
        font-weight: lighter;
    }

    .ColSeleEleAcer {
        padding-right: 5rem;
    }

    .ElemAcer img {
        width: 20rem;
    }

    .ColElemAcer img {
        width: 20rem;
    }

    .ColBrochure img {
        width: 20rem;
        margin-bottom: 2rem;
    }

    .RowTitBroAcer h1 {
        margin-left: 5rem;
    }

    .ColInfoBroAcer {
        text-align: left;
        padding-right: 6rem;
    }

    .RowTextBroAcer p {
        font-size: 1.7rem;
        font-weight: lighter;        
    }

    .RowTitFichaAcerca {
        width: 100%;
    }

    .RowTitFichaAcerca h1 {
        margin-left: 4rem;
    }

    .RowImaFichaAcer img {
        width: 22rem;
        margin-bottom: 2rem;
    }

    .RowTextoFichaAcer p{
        font-size: 1.7rem;
        font-weight: lighter;
        text-align: left;
        margin-bottom: 3rem;
    }    

    .AudioFichaAcer {
        width: 20rem;
    }

    .RowTextoFichaAcer {
        padding: 0 5rem;
    }

    .RowInfoExtraFichaAcer {
        padding-right: 5rem;
        margin-top: 5rem;
    }

    .ContaiFichaAcer {
        margin-top: 9rem;
    }

    .RowPlaneAcer h1{
        margin-left: 4rem;
    }

    .ColInfoPlaneAcer {
        padding-left: 6rem;
    }

    .ColInfoPlaneAcer p {
        font-size: 1.7rem;
        font-weight: lighter;
        text-align: left;
        margin-bottom: 2rem;
    }

    .AudioPlaneAcer {
        width: 30rem;
    }

    .ColFasesPlaneAcer h2{
        font-size: 2.5rem;
        padding-left: 7rem;
        margin-bottom: 3rem;
    }

    .ColFasesPlaneAcer {
        padding-right: 9rem;
    }

    .ColFasesPlaneAcer img{
        width: 20rem;
    }

    .ContainPlaneadorAcer {
        margin-top: 8rem;
    }

    .RowTitActvPlaneAcer {
        margin-top: 4rem;
    }

    .RowTitActvPlaneAcer h1{
        margin-left: 5rem;
        font-size: 2.7rem;
        font-weight: normal;
    }

    .RowTitActivi1PlanAcer {
        margin-bottom: 1rem;
    }

    .RowConteAct1PlanAcer p{
        font-size: 1.7rem;
        font-weight: lighter;
        text-align: left;
    }

    .RowConteAct1PlanAcer img {
        width: 20rem;
    }

    .ColActiPlanAcer {
        padding: 0 5rem;
    }

    .ColListPlanAcer h2{
        font-size: 2.5rem;
    }

    .RowTitRecurPlaneAcer{
        width: 100%;
        margin-left: 5rem;
    }

    .RowTitRecurPlaneAcer h2{
        font-size: 2.5rem;
    }

    .RowListRePlanAcer {
        padding-left: 3rem;
        margin-top: 1.5rem;
    }

    .RowListRePlanAcer li {
        font-size: 1.7rem;
        font-weight: lighter;
    }

    .RowContenListPlanAcer {
        margin-top: 5rem;
    }

    .RowBotDesPlaneAcer {
        width: 100%;
        padding-left: 3rem;
        margin-top: 1.8rem;
    }

    .RowImagPlanAc img{
        width: 20rem;
    }

    .RowImagPlanAc {
        margin-bottom: 2rem;
    }

    .ColImgPlaneAcer {
        padding-right: 15rem;
    }

    .ColListPlanAcer {
        padding-left: 15rem;
    }

    .RowElemeAcer {
        margin-bottom: 5rem;
        margin-top: 8rem;
    }

    .ContaiElemAcerca {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .ContaiBroAcer {
        display: flex;
        flex-direction: column;
    }

    .RowInfoBroAcer {
        display: flex;
        align-items: center;
    }

    .RowTextBroAcer {
        margin-bottom: 3rem;
    }

    .AudioBrochureAcer {
        width: 30rem;
    }

    .RowImgfasesPlaneAcer img{
        width: 30rem;
    }
}