*{                                                                                                  /*Timeline*/
    box-sizing: border-box;
}
  
.timeline-container{
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 40px 0;
}
  
.timeline-container::after{
  background-color: #2997D4; /* Color de la linea del centro*/
  content: '';
  position: absolute;
  left: calc(50% - 2px); /* Posicion de la barra*/
  width: 4px;
  height: 100%;
  z-index: 0;
}
  
  .timeline-item{
    display: flex;
    justify-content: flex-end;
    padding-right: 28.08rem;
    margin: 10px 0;
    width: 80%; /* Ancho del item estaba en 50*/
  }
  
  .timeline-item:nth-child(odd){
    align-self: flex-end;
    justify-content: flex-start;
    padding-right: 0;
    padding-left: 28.08rem;
  }
  
  .timeline-item-content{
    max-width: 80%;
    background-color: #beccdf;
    border-radius: 1.5rem;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: relative;
    padding: 2rem;
    width: 700px;
    text-align: right;
  }
  
  .timeline-item-content::after{
    content: '';
    background-color: #beccdf;
    border-radius: 4px;
    box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.25);
    position: absolute;
    transform: rotate(45deg);
    right: -7.5px;
    top: calc(50% - 7.5px);
    width: 15px;
    height: 15px;
  
  }
  
  .timeline-item:nth-child(odd) .timeline-item-content{
    align-items: flex-start;
    text-align: left;
  }
  
  .timeline-item:nth-child(odd) .timeline-item-content::after{
    box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.25);
    right: auto;
    left: -7.5px;
  }
  
  .timeline-item-content time{
    font-size: 2rem;
    font-weight: bold;
    font-family: 'Quicksand', sans-serif;
    color: #2997D4;
  }
  
  .timeline-item-content p{
    font-size: 1rem;
    line-height: 24px;
    margin: 15px 0;
    max-width: 250px;
    color: black;
    font-weight: 500;
  }

  .timeline-info {
    display: flex;
    align-items: center;
  }

  .timeline-item-content .timeline-img {
    display: flex;
    align-self: center;
    max-width: 100%;
  }

  .timeline-item-content img{
    width: 15rem;
    margin-left: 1rem; 
    border: #2997D4;
    border-style: solid;
    border-width: 0.3rem;   
  }
    
  .timeline-item-content .circle{
    background-color: #beccdf;
    border: 3px solid #2997D4;
    border-radius: 50%;
    position: absolute;
    top: calc(50% - 10px);
    right: -40px;
    width: 20px;
    height: 20px;
    z-index: 100;
  }
  
  .timeline-item:nth-child(odd) .timeline-item-content .circle{
    right: auto;
    left: -40px;
  }

  .timeline-item-content .tag{
    font-family: 'Quicksand', sans-serif;
    color: white;
    font-size: 1rem;
    font-weight: bold;
    padding: 5px;
    position: absolute;
    letter-spacing: 1px;
    top: 0.9rem;
    left: 0.9rem;
    border-radius: .5rem;
  }
    
  .timeline-item:nth-child(odd) .timeline-item-content .tag{
    left: auto;
    right: 0.9rem;
  }                                                                             /*Timeline*/

.ContHist {
    background-image: url('../Images/Fondo2.png');
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    /* background-size: cover; */
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
}

.InfoHisto {
    display: flex;
    flex-direction: column;
}

.conteniInfo {
    display: flex;
    align-items: center;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
  .timeline-item-content .tag{
    width: calc(100% - 10px);
    top: 0.35rem;
    left: 0.325rem;
  }

  .timeline-item:nth-child(odd) .timeline-item-content .tag{
    left: auto;
    right: 0.325rem;
  }
  .timeline-item{
    padding-right: 2rem;
    width: 50%; /* Ancho del item estaba en 50*/
  }
  
  .timeline-item:nth-child(odd){
    padding-left: 2rem;
  }

  .timeline-item-content{
    min-width: 100%;
    border-radius: 0.5rem;
  }

  .timeline-item-content, .timeline-item:nth-child(odd) .timeline-item-content{
    padding: 15px 10px;
    text-align: center;
    align-items: center;
  }

  .timeline-item-content time{
    margin-top: 20px;
  }

  .timeline-item-content img{
    width: 80%;
    height: auto;
  }

  .timeline-item-content{
    width: 18rem;
  }

    .conteniInfo {
        flex-direction: column;
    }

    .conteniInfo img {
        width: 18rem;
    }

    .contaiHistor {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .col2 h2 {
        font-size: 1.5rem;
    }

    .titlHis {
        margin-top: 1rem;
    }

    .InfoHisto {
        margin-bottom: 0.7rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) { 
    .conteniInfo {
        flex-direction: column;
    }

    .conteniInfo p {
        font-size: 1.4rem;
        font-weight: lighter;
    }

    .conteniInfo img {
        width: 35rem;
    }

    .contaiHistor {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .contaiHistor h1 {
        font-size: 2.5rem;
    }

    .col2 h2 {
        font-size: 2.2rem;
        margin-bottom: 1.2rem;
    }

    .titlHis {
        margin-top: 3rem;
    }

    .InfoHisto {
        margin-bottom: 3rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .conteniInfo p{
        margin-right: 3rem;
        font-size: 1.5rem;
        font-weight: lighter;
    }

    .contaiHistor h1 {
      font-size: 4rem;
      margin-top: 4.5rem;
      margin-left: 5rem;
    }

    .col2 h2 {
        font-size: 2.5rem;
        margin-bottom: 3rem;
    }

    .col2 {
        margin-bottom: 7rem;
    }
}