.TodoImport {
    background-image: url('../../Images/Fondo2.png');
    background-size: cover;
    font-family: 'Quicksand', sans-serif;
    min-height: 92.2vh;
    color:white;
}

.ContaiImportan {
    display: flex;
    /* align-items: center; */
    justify-content: center;
}

.RowTextImport p{
    font-weight: lighter;
}

.RowEnlaces {
    display: flex;
    flex-direction: column;
}

.RowEnlaces a{
    font-weight: lighter;
    font-style: italic;
    text-decoration: none;
    color: white;
}

.BotnBac {
    position: fixed;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .ContaiImportan {
        flex-direction: column;
    }

    .BotnBac {
        left: 1rem;
        bottom: 1rem;
    }

    .ColInfoImport {
        margin-top: 1rem;
    }

    .RowEnlaces {
        text-align: left;
        padding-left: 1rem;
        margin-top: 1rem;
    }

    .RowEnlaces h2 {
        font-size: 1.3rem;
    }

    .RowEnlaces a{
        padding: 0.3rem 0rem;
    }

    .ColImgImporta img {
        width: 15rem;
    }

    .ColImgImporta {
        margin: 2rem 0;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 1200px) { 
    .ContaiImportan {
        flex-direction: column;
    }

    .RowTitImportan h1 {
        font-size: 3rem;
    }

    .ColInfoImport {
        margin-top: 2rem;
    }

    .RowEnlaces {
        text-align: left;
        margin-top: 1rem;
    }

    .BotnBac {
        left: 2rem;
        bottom: 2rem;
    }

    .RowTextImport p {
        font-size: 1.5rem;
        padding: 0 1rem;
    }

    .RowEnlaces h2 {
        font-size: 2rem;
        padding-left: 2rem;
    }

    .RowEnlaces a{
        padding: 0.3rem 2rem;
        font-size: 1.3rem;
    }

    .ColImgImporta img {
        width: 30rem;
    }

    .ColImgImporta {
        margin: 3rem 0;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .RowTitImportan h1 {
        font-size: 4rem;
    }

    .ContaiImportan{
        padding-top: 3.5rem;
    }

    .ColInfoImport{
        text-align: left;
        padding-left: 5rem;
    }

    .RowTextImport p{
        font-size: 1.7rem;
    }

    .RowEnlaces {
        margin-top: 3rem;
    }

    .RowEnlaces a{
        font-size: 1.3rem;
        padding: 0.5rem 0;
    }

    .BotnBac {
        left: 2rem;
        bottom: 2rem;
    }

    .ColImgImporta img {
        width: 35rem;
    }
}