.TodoNoInmerIma {
    background-image: url('../../Images/RV/FondoRV.png');
    background-size: cover; 
    background-position: center center;   
    color: white;
    font-family: 'Quicksand', sans-serif;
    min-height: 92.2vh;
}

.ContainRVNo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowtitRVNo {
    width: 100vw;
}

.RowInfoRVNo{
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .btnB {
        position: absolute;
        bottom: 2rem;
        left: 2rem;
    }

    .RowtitRVNo {
        padding-left: 5rem;        
    }

    .RowInfoRVNo {
        margin-top: 2rem;
    }

    .RowInfoRVNo img{
        width: 35rem;
        margin-bottom: 3rem;
        padding-left: 2rem;
    }

    .ContainRVNo {
        padding-top: 3.8rem;
    }

    .AudioRVNo {
        width: 30rem;
    }

    .RowtitRVNo h1 {
        font-size: 4rem;
    }
}