.TodoMarker {
    background-image: url('../Images/Fondo2.png');
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 92.2vh;
    color:white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
}

#Concepto, #Aplicaciones, #MarcadoresRA {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 92.2vh;
    margin: 0;
}

.ContaiMarker {
    display: flex;
    flex-direction: column;
    align-items: center;   
}

.TitRAMarker {
    width: 100%;
}

.RowInfoMarker {
    display: flex;
    align-items: center;
}

.MenuBo {
    display: flex;
}

.MenuBo .Campo {
    color: white;
    font-family: 'Quicksand', sans-serif;
    font-size: 1.3rem;
}

.Bottombar {
    width: 100%;
    background-color: #063E66 ;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
}

.BotVolType {
    position: fixed;
}

.ContaiRAApps {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.RowTitRAApps {
    width: 100%;
    display: flex;
}

.RowApp {
    display: flex;
    align-items: center;
}

.ColInfoRAApp {
    display: flex;
    flex-direction: column;
}

.ColInfoRAApp2 {
    display: flex;
    flex-direction: column;
}

.ContaiConsejos {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RowInfoConsejos {
    display: flex;
    align-items: center;
}

.ColInfoConsejos {
    display: flex;
    flex-direction: column;
}

.aLink span{    
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.aLink span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.aLink:hover span {
    padding-right: 25px;
}

.aLink:hover span:after {
    opacity: 1;
    right: 0;
}

.TodoMarker strong {
    font-weight: bolder;
}

.ColImgMarker img {
    border: white;
    border-style: solid;
    border-width: 0.3rem;
}

.VideoMarker{
    border-color: white;
    border-style: solid;
}

.ColInfoRAApp a {
    text-decoration: none;
    text-align: center;
    background-color:white;
    color: #2997D4;    
    border-radius: 2.5rem;
    border-style: solid;
    border-width: 0.2rem;
    border-color: #2997D4;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
}

.ColInfoRAApp2 a {
    text-decoration: none;
    text-align: center;
    background-color:white;
    color: #2997D4;    
    border-radius: 2.5rem;
    border-style: solid;
    border-width: 0.2rem;
    border-color: #2997D4;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
}

.ColInfoConsejos a {
    text-decoration: none;
    text-align: center;
    background-color:white;
    color: #2997D4;    
    border-radius: 2.5rem;
    border-style: solid;
    border-width: 0.2rem;
    border-color: #2997D4;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .RowInfoMarker {
        flex-direction: column;
    }

    .ColInfoRAApp {
        align-items: center;
    }

    .ColInfoRAApp2 {
        align-items: center;
    }

    .RowInfoMarker p {
        font-weight: lighter;
        margin-bottom: 2rem;
    }

    .MenuBo .Campo {
        font-size: 0.7rem;
    }

    .VideoMarker {
        width: 19rem;
        height: 11rem;
        border-width: 0.3rem;
    }

    .TitRAMarker{
        margin-top: 0;
    }

    .BotVolType {
        bottom: 2.7rem;
        left: 1rem;
    }

    .RowTitRAApps {
        justify-content: center;
    }

    .RowApp {
        flex-direction: column-reverse;
    }

    .ColInfoRAApp h2{
        font-size: 1.7rem;
    }

    .ColInfoRAApp2 h2{
        font-size: 1.7rem;
    }

    .ColInfoRAApp p{
        font-weight: lighter;
        margin-top: 0.8rem;
    }

    .ColImgRAApp img {
        width: 12rem;
    }

    .ColImgRAApp {
        margin-bottom: 2rem;
    }

    .ColInfoRAApp2 p{
        font-weight: lighter;
        margin-top: 0.8rem;
    }

    .ColImgRAApp2 img {
        width: 12rem;
    }

    .ColImgRAApp2 {
        margin-bottom: 2rem;
    }

    .RowInfoConsejos {
        flex-direction: column;
    }

    .ColImgMarker img {
        width: 19rem;
    }

    .ColInfoConsejos{
        align-items: center;
    }

    .ColInfoConsejos p{
        font-weight: lighter;
        padding: 0 1rem;
        margin-top: 2rem;
        margin-bottom: 1.5rem;
    }

    .ColInfoRAApp a {
        width: 6rem;
        height: 2.5rem;
        font-size: 1.1rem;
        padding-top: 0.2rem;
    }

    .ColInfoRAApp2 a {
        width: 6rem;
        height: 2.5rem;
        font-size: 1.1rem;
        padding-top: 0.2rem;
    }

    .ColInfoConsejos a {
        width: 9rem;
        height: 2.5rem;
        font-size: 1.1rem;
        padding-top: 0.15rem;
    } 
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) { 
    .BotVolType {
        bottom: 3.9rem;
        left: 1.2rem;
    }

    .RowTitRAApps h1 {
        font-size: 2.7rem;
    }

    .RowInfoMarker p {
        font-size: 1.7rem;
        font-weight: lighter;
        padding: 0 1rem;
        margin-bottom: 2rem;
    }

    .VideoMarker {
        border-width: 0.4rem;
    }

    .RowApp {
        flex-direction: column-reverse;
    }

    .ColInfoRAApp, .ColInfoRAApp2 {
        align-items: center;
        margin-bottom: 4rem;
    }

    .ColInfoRAApp h2{
        font-size: 2.3rem;
    }

    .ColInfoRAApp p{
        font-size: 1.7rem;
        font-weight: lighter;
        margin-top: 1rem;
        padding: 0 1.3rem;
    }

    .ColImgRAApp img {
        width: 25rem;
    }

    .ColImgRAApp {
        margin-bottom: 2.5rem;
    }

    .ColInfoRAApp2 h2{
        font-size: 2.3rem;
    }

    .ColInfoRAApp2 p{
        font-size: 1.7rem;
        font-weight: lighter;
        margin-top: 1rem;
        padding: 0 1.3rem;
    }

    .ColImgRAApp2 img {
        width: 25rem;
    }

    .ColImgRAApp2 {
        margin-bottom: 2.5rem;
    }

    .ColImgMarker img {
        width: 39rem;
    }

    .ColInfoConsejos p{
        font-size: 1.7rem;
        font-weight: lighter;
        padding: 0 1.3rem;
        margin-top: 2.3rem;
        margin-bottom: 1.5rem;
    }

    .ColInfoConsejos {
        margin-bottom: 3rem;
        align-items: center;
    }

    .ColInfoRAApp a {
        width: 9rem;
        height: 3rem;
        font-size: 1.5rem;
        padding-top: 0.2rem;
    }

    .ColInfoRAApp2 a {
        width: 9rem;
        height: 3rem;
        font-size: 1.5rem;
        padding-top: 0.2rem;
    }

    .ColInfoConsejos a {
        width: 10rem;
        height: 3rem;
        font-size: 1.5rem;
        padding-top: 0.15rem;
    } 
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .TitRAMarker h1{
        padding-left: 4rem;
        margin-bottom: 8rem;
        font-size: 4rem;
    }

    .RowInfoMarker p {
        font-size: 1.7rem;
        font-weight: lighter;
        text-align: left;
        padding-left: 4rem;
    }

    .MenuBo .Campo {
        margin: 0 3rem;
    }

    .VideoMarker {
        width: 49rem;
        height: 28rem;        
        border-width: 0.5rem;
        animation-name: videoin;
        animation-duration: 0.5s;
    }

    .VideoMarker {animation-timing-function: ease-in;}

    @keyframes videoin {
        0% {transform: translateX(15rem); opacity: 0;}
        50% {opacity: 1;}
    }

    .BotVolType {
        bottom: 4rem;
        left: 1.7rem;
    }

    .RowTitRAApps h1{
        padding-left: 4rem;
        font-size: 4rem;
    }

    .ColInfoRAApp {
        align-items: flex-start;
        text-align: left;
        padding-left: 10rem;
    }

    .ColInfoRAApp h2{
        font-size: 2.5rem;
    }

    .ColInfoRAApp p{
        font-size: 1.7rem;
        font-weight: lighter;
    }    

    .ColImgRAApp img {
        width: 20rem;      
    }

    .ColImgRAApp {
        padding-right: 13rem;
        display: flex;
        justify-content: flex-start;
    }

    .ColInfoRAApp2 {
        align-items: flex-start;
        text-align: left;
        padding-right: 10rem;
        margin-top: 3rem;
    }

    .ColInfoRAApp2 h2{
        font-size: 2.5rem;
    }

    .ColInfoRAApp2 p{
        font-size: 1.7rem;
        font-weight: lighter;
    }

    .ColImgRAApp2 img {
        width: 30rem;
    }

    .ColImgRAApp2 {
        display: flex;
        justify-content: flex-end;
        padding-right: 6rem;
        margin-top: 3rem;
    }

    .ColImgMarker img {
        width: 50rem;
    }

    .ColInfoConsejos {
        align-items: flex-start;
        text-align: left;
    }

    .ColInfoConsejos p{
        font-size: 1.7rem;
        font-weight: lighter;
        margin-bottom: 3rem;
        padding-right: 8rem;
    }

    .RowInfoConsejos {
        margin-top: 5rem;
        margin-bottom: 10rem;
    }

    .ContaiRAApps {
        margin-top: 5rem;
    }

    .ColInfoRAApp a {
        width: 8.5rem;
        height: 3rem;
        font-size: 1.6rem;
    }

    .ColInfoRAApp2 a {
        width: 8.5rem;
        height: 3rem;
        font-size: 1.6rem;
    }

    .ColInfoConsejos a {
        width: 13rem;
        height: 3rem;
        font-size: 1.6rem;
    }  

    .TodoMarker strong {
        font-size: 1.8rem;
    }
}