.TodoHerraRA {
    background-image: url('../../Images/Fondo2.png');
    background-size: cover;
    display: flex;
    align-items: center;
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
}

.VideoAssemblrStudio {
    border: white;
    border-style: solid;
}

.ColInfoAssSt p{
    font-weight: lighter;
}

.ColInfoAssApp p{
    font-weight: lighter;
}

.ColInfoAssSt strong {
    font-weight: bolder;
}

.RowAssStudio {
    display: flex;
}

.ColInfoAssSt a {
    color: yellow;
}

.BotVolExp {
    position: fixed;
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .BotVolExp {
        bottom: 3.9rem;
        left: 1.2rem;
    }

    .RowTitHerraRA h1 {
        font-size: 2.7rem;
        margin-top: 1rem;
    }

    .RowAssStudio {
        flex-direction: column;
    }

    .ColInfoAssSt {
        margin-top: 1.5rem;
    }

    .ContaiAssStudio {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .BotVolExp {
        bottom: 1.7rem;
        left: 1.7rem;
    }

    .VideoAssemblrStudio {
        border-width: 0.5rem;
        /* width: 49rem;
        height: 28rem; */
        animation-name: videoina;
        animation-duration: 0.5s;
    } 

    .VideoAssemblrStudio {animation-timing-function: ease-in;}

    @keyframes videoina {
        0% {transform: translateX(-15rem); opacity: 0;}
        50% {opacity: 1;}
    }
    
    .RowTitHerraRA h1 {
        font-size: 4rem;
    }
    
    .ColInfoAssSt {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .ColInfoAssApp {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-left: 8rem;
    }

    .ColInfoAssSt p{
        text-align: left;
        font-size: 1.7rem;
        padding-right: 8rem;
        margin-top: 1.2rem;
    }

    .ColInfoAssApp p{
        text-align: left;
        font-size: 1.7rem;
        margin-top: 1.2rem;
        padding-right: 5rem;        
    }

    .ContaiAssStudio {
        padding-left: 6rem;
        margin-bottom: 3rem;
    }

    .RowTitHerraRA {
        margin-top: 3.5rem;
    }

    .RowAssStudio {
        align-items: center;
    }
}