.ButtonHome {
    display: inline-block;
    background-color:#2997D4;
    text-decoration: none;
    color: white;    
    border-radius: 2.5rem;
    border-style: none;
    text-align: center;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    width: 13rem;
    height: 3.2rem;
    font-size: 1.6rem;
    font-family: 'Quicksand', sans-serif;
    font-weight: bold;
    transition: all 0.5s;
    cursor: pointer;
}

/* .ButtonHome:hover{
    background-color: white;
    color: #2997D4;
    border: #2997D4;
    border-style: solid;
    border-width: 0.3rem;
} */

.ButtonHome span{
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.ButtonHome span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.ButtonHome:hover span {
    padding-right: 25px;
}

.ButtonHome:hover span:after {
    opacity: 1;
    right: 0;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) { 
    .ButtonHome {
        width: 9rem;
        height: 2.7rem;
        font-size: 1rem;
    }
}
















