.tipos {
    background-image: url('../Images/Fondo2.png');
    background-size: cover;
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
}

.CardsRA {
    display: flex;
    justify-content: center;
    margin-left: 10rem;
}

.CardsRA img {
    width: 19rem;
    margin: 1.5rem;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) { 
    .CardsRA {
        flex-direction: column;
        margin-left: 0;
    }

    .CardsRA img {
        width: 11.3rem;
        margin: 0.5rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .CardsRA {
        margin-left: 0;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .TitTip h1{
        margin-left: 5rem;
        margin-top: 4rem;
        margin-bottom: 6rem;
        font-size: 4rem;
    }

    .Cards {
        display: flex;
    }

    .Cards .Card {
        display: flex;
        flex-direction: column;
    }

    .Card .boto {
        position: absolute;
        bottom: 16rem;
        left: 28.1rem;
    }

    .Card .boto2 {
        position: absolute;
        bottom: 16rem;
        left: 50.2rem;
    }

    .Card .boto3 {
        position: absolute;
        bottom: 16rem;
        left: 72.3rem;
    }

    .Card .boto4 {
        position: absolute;
        bottom: 16rem;
        left: 94.4rem;
    }
}