.Sidebar {    
    flex-direction: column;
    font-family: 'Quicksand', sans-serif;  
    background-image: url('../Images/Fondo_Side.svg');
    background-repeat: no-repeat;
    background-position: center;  
}

.Celda {
    display: flex;    
}

.Sidebar a {
    color: white;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .contentSidebar {
        position: fixed;
        z-index: 1000;
        bottom: 2rem;
        width: 100vw;
        display: flex;
        justify-content: center;
    }

    .contentSidebar .bot {
        display: none;
    }

    .Sidebar {
        flex-direction: row;
        background-image: none;
    }

    .Celda img {
        display: none;
    }

    .Sidebar a {
        font-size: 1rem;
        padding: 0 0.3rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) { 
    .Sidebar {
        flex-direction: row;
        background-image: none;
    }

    .contentSidebar {
        position: fixed;
        z-index: 1000;
        bottom: 2.5rem;
        width: 100vw;
        display: flex;
        justify-content: center;
    }

    .contentSidebar .bot {
        display: none;
    }

    .Celda img {
        display: none;
    }

    .Sidebar a {
        font-size: 1.4rem;        
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .Sidebar {
        position: fixed;        
        height: 92.2vh;
        justify-content: center;
        width: 14rem;
        font-size: 1.5rem;
        text-align: left;
    }

    .Celda {
        margin-bottom: 1.5rem;
        transition: transform .2s; /* Animation */
        margin: 0 auto;        
    }

    .Celda:hover {
        transform: scale(1.2);
    }

    .Celda img {
        width: 2rem;
        margin-right: 0.5rem;
    }
}