.ButtonCard {
    display: inline-block;
    background-color:#FD9100;
    text-decoration: none;
    color: white;    
    border-radius: 2.5rem;
    border-style: none;
    text-align: center;
    box-shadow: 0.12rem 0.12rem 0.4rem rgba(0, 0, 0, 0.5);
    width: 9rem;
    height: 3rem;
    font-size: 1.6rem;
    font-family: 'Quicksand', sans-serif;
    font-weight: 700;
    transition: all 0.5s;
    cursor: pointer;
}

.ButtonCard span{
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.ButtonCard span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.ButtonCard:hover span {
    padding-right: 25px;
}

.ButtonCard:hover span:after {
    opacity: 1;
    right: 0;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) { 
    .ButtonCard {
        width: 7rem;
        height: 2.5rem;
        font-size: 1rem;
    }
}