.TodoPerife {
    background-image: url('../../Images/Fondo2.png');
    background-size: cover;    
    color: white;
    font-family: 'Quicksand', sans-serif;
    min-height: 92.2vh;
}

.ContaiPerife {
    display: flex;
    flex-direction: column;
}

.RowTitPerife {
    display: flex;
}

.RowTextPerife {
    display: flex;
}

.RowTextPerife p {
    font-weight: lighter;
}

.RowImagPerife {
    display: flex;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) {
    .TodoPerife {
        background-image: url('../../Images/FondoMovil.svg');
        background-position: center center;
        background-attachment: fixed;
    }

    .ContaiPerife {
        padding-top: 1rem;
    }

    .RowTitPerife {
        justify-content: center;
        padding: 0 1rem;
    }

    .RowTextPerife {
        padding: 0 1rem;
    }

    .RowImagPerife {
        flex-direction: column;
    }

    .ColImgPer img{
        width: 18rem;
    }

    .ColImgPer, .ColImgPer2 {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .ColImgPer2 {
        margin-bottom: 3rem;
    }

    .AudioPerifeEntra, .AudioPerifeSalida {
        width: 10rem;
        margin-top: 1rem;
    }

    .ColImgPer2 img{
        width: 17rem;
        margin-top: 2rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) { 
    .RowTitPerife {
        justify-content: center;
    }  
    
    .RowTitPerife h1 {
        font-size: 3rem;
    }

    .RowTextPerife p {
        font-size: 1.5rem;
    }

    .ContaiPerife {
        padding: 1rem 3rem;
    }
}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .ContaiPerife{
        padding-top: 3.4rem;
    }

    .RowTitPerife{
        padding-left: 5rem;
    }

    .RowTitPerife h1 {
        font-size: 4rem;
    }

    .RowTextPerife {
        padding-left: 25rem;
        padding-right: 15rem;
        margin-bottom: 2.7rem;
    }

    .RowTextPerife p{
        text-align: left;
        font-size: 1.7rem;
    }

    .RowImagPerife{
        padding-left: 20rem;
        padding-right: 15rem;
    }

    .ColImgPer {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .ColImgPer img{
        width: 30rem;
    }

    .ColImgPer2 img{
        width: 26rem;
    }

    .AudioPerifeEntra {
        width: 13rem;
        position: absolute;
        bottom: 0.8rem;
        left: 13rem;
    }

    .AudioPerifeSalida {
        width: 13rem;
        position: absolute;
        bottom: 0.8rem;
        left: 15rem;
    }

    .RowTextPerife strong {
        font-weight: bolder;
        font-size: 1.8rem;
    }
}