.tipos {
    background-image: url('../../Images/Fondo2.png');
    background-size: cover;
    min-height: 92.2vh;
    color: white;
    font-family: 'Quicksand', sans-serif;
}

.CardsRV {
    display: flex;
    justify-content: center;    
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) { 
    .TitTip {
        display: flex;
        justify-content: center;
        padding-top: 1rem;
    }

    .CardsRV {
        flex-direction: column;
        margin-left: 0;
    }

    .CardsRV img {
        width: 11.3rem;
        margin: 0.5rem;
    }

    .Card1 {
        margin-bottom: 1rem;
    }

    .Card1 .boto {
        position: absolute;
        top: 21.6rem;
        left: 8.4rem;
    }

    .Card1 .boto2 {
        position: absolute;
        top: 39.05rem;
        left: 8.4rem;
    }

    .Card1 .boto3 {
        position: absolute;
        top: 56.6rem;
        left: 8.4rem;
    }
}

/*Medium devices (tablets, 768px and up)*/
@media (min-width: 768px) and (max-width: 991.98px) {
    .TitTip {
        display: flex;
        justify-content: center;
        padding-top: 2rem;
    }

    .TitTip h1 {
        font-size: 3rem;
    }

    .Cards {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .Card1 {
        margin: 1rem 2rem;
    }

    .CardsRV img {
        width: 14rem;
        margin: 0.5rem;
    }

    .Card1 .boto {
        position: absolute;
        top: 29.9rem;
        left: 10.5rem;
    }

    .Card1 .boto2 {
        position: absolute;
        top: 29.9rem;
        right: 10rem;
    }

    .Card1 .boto3 {
        position: absolute;
        bottom: 9.1rem;
        left: 19.85rem;
    }

}

/*Extra large devices (large desktops, 1200px and up)*/
@media (min-width: 1200px) {
    .TitTip h1{
        margin-left: 5rem;
        margin-top: 4rem;
        margin-bottom: 6rem;
        font-size: 4rem;
    }

    .CardsRV {
        margin-left: 10rem;
    }

    .CardsRV img {
        width: 19rem;
        margin: 1.5rem;
    }

    .Cards {
        display: flex;
    }

    .Card1 {
        margin: 0 2rem;
    }

    .Cards .Card1 {
        display: flex;
        flex-direction: column;
    }

    .Card1 .boto {
        position: absolute;
        bottom: 16rem;
        left: 35.3rem;
    }

    .Card1 .boto2 {
        position: absolute;
        bottom: 16rem;
        left: 61.5rem;
    }

    .Card1 .boto3 {
        position: absolute;
        bottom: 16rem;
        left: 87.4rem;
    }
}