.TodoObject {
    background-image: url('../../Images/Fondo2.png');
    background-position: center center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 92.2vh;
    color:white;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
}

#ConceptoOb, #AplicacionesOb, #ObjectsRA {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 92.2vh;
    margin: 0;
}

.ContaiObject {
    display: flex;
    flex-direction: column;
    align-items: center;   
}

.TitRAObject {
    width: 100%;
}

