.Menu{
    background-color: #063E66 ;
    font-family: 'Bebas Neue', cursive;
    font-weight: 400;
    font-size: 1.3rem;
    padding-left: 5rem;
    padding-right: 5rem;
    letter-spacing: 0.1rem;
}

.NavText{
    margin-left: 1rem;
    font-size: 1.5rem;
    letter-spacing: 0.2rem;
}

/*Extra small devices (portrait phones, less than 576px)*/
@media (max-width: 575.98px) { 
    .Menu{
        padding-left: 2rem;
        padding-right: 2rem;
        font-size: 1rem;
    }

    .NavText{
        font-size: 0.9rem;
        margin-left: 0.5rem;
        letter-spacing: 0rem;
    }

    .o-imgbrand{
        width:30;
    }
}

